
.title {
    background-color: #ffbf87;
    font-size: 1em;
    margin-bottom: 5px;
    margin-top: 25px;
    position: relative;
    text-align: center;
    border-radius: 15px;
    width: 280px;
    height: 45px;
  }
  .parrafo-info {
    text-align: center;
    font-size: 16px;
    margin-top: 20px;
    margin-bottom: 20px;
    line-height: 1.5;
    text-justify: distribute;
  }
  .zuki-head {
    width: 150px;
    height: 150px;
    border-radius: 90px;
    border: 5px solid #fff;
    position: relative;
  }
  .cont-info {
    background-color: #feebb8;
  }
  .close {
    border-radius: 50px;
    height: 60px;
    width: 60px;
    transform: translateX(190px) translateY(30px);
    background-color: #f8d578;
    border: 0;
    box-shadow: 2px 7px 10px rgba(180, 180, 180, 50%);
  }
  .close:hover {
    background-color: #e3c36e;
  }