.cont-mision {
    background-color: #d7efbf;
}

.mision-opcion {
    display: flex;
}
.mision-header{
    height: 65px;
    width: 65px;
    border-radius: 35px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}
.binoculares {
    width: 50px;
}
.text {
    font-size: 16px;
}
.botones {
    display: flex;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.col-6 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
}

.button {
    border-radius: 20px;
    border: none;
    padding: 20px;
    text-wrap: nowrap;
    box-shadow: 2px 7px 10px rgba(180, 180, 180, 50%);
    background-color: #f6ffed;

}
.texto-button {
    color: #213f04;
    font-size: 14px!important;
}

.mision-color {
    background-color: #608d32;
}
.mision-color:hover {
    background-color: #2c490e;
}

.button:hover {
    background-color: #afcb93;
}
.tabla {
    padding-top: 20px;
}
.espacio-check {
    min-width: 20px;
}
.punto {
    height: 10px;
    width: 10px;
    border-radius: 15px;
}
.mision {
    max-width: 220px;
    padding-top: 5px;
    padding-bottom: 5px;
}
.experiencia {
    min-width: 20px;
}
.close {
    border-radius: 50px;
    height: 60px;
    width: 60px;
    background-color: #608d32;
    border: 0;
    box-shadow: 2px 7px 10px rgba(180, 180, 180, 50%);
    color: #ffffff;
} 
.close:hover {
    background-color: #213f04;
    color: #ffffff;
} 