.afuera {
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.cont-zorro {
  background-color: #fbdbb8;
}
.zindex1 {
  z-index: 1;
}
.fotito {
  width: 80%;
  height: auto;
  border-radius: 7%;
  position: relative;
}

.titulo {
  position: relative;
  transform: translateX(0px) translateY(6px);
  text-align: center;
  border-radius: 15px;
}

.parrafo {
  text-align: center;
  font-size: 16px;
  margin: 20px 20px;
  line-height: 1.2;
  text-justify: distribute;
}

.close:hover {
  background-color: #de8639;
}

.hr {
  border-top-width: 1px;
  border-top-style: solid;
  border-top-color: #000;
}
