.cont-lectura {
    background-color: #FED3C7;
}
.contenido-lectura {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-height: 300px;
    margin-top: 50px;
}
.boton-lectura {
    padding: 20px;
    border-radius: 20px;
    min-width: 250px;
    border: none;
    background-color:#fa9e9e;
    text-decoration: none;
    text-align: center;
    color: var(--negro);
}
.boton-lectura:hover {
    background-color:#fc6262;
}
.close {
    background-color:#fa9e9e;
}
.close:hover {
    background-color:#fc6262;
}