/* CREAR USUARIO */
.wrapper {
  width: 420px;
  max-height: 100%;
  background: transparent;
  border: 2px solid var(--blanco33);
  backdrop-filter: blur(50px);
  box-shadow: 0 0 10px var(--negro33);
  color: var(--blanco);
  border-radius: 35px;
  padding: 30px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper h1 {
  font-size: 36px;
  text-align: center;
}

.wrapper .input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin: 30px 0;
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  border: 2px solid var(--blanco33);
  border-radius: 40px;
  font-size: 16px;
  color: var(--blanco);
  padding: 20px 45px 20px 20px;
}

.input-box select {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  border: 2px solid var(--blanco33);
  border-radius: 40px;
  font-size: 16px;
  color: var(--blanco);
  padding: 13px 45px 17px 20px;
}

.input-box input::placeholder {
  color: var(--blanco);
}

.input-box i {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
}


.wrapper .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 45px;
  background: var(--blancoa1);
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px var(--negro1a);
  color: var(--browser);
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin: 0 auto;
  padding: 0;
  transition: background-color 0.3s, color 0.3s;
}

.wrapper .btn:hover {
  background: var(--blanco);
  color: var(--Yoshi);
}

.btn:disabled {
  background: #ffffff38;
  cursor: not-allowed;
  /* Change the cursor to indicate it's not clickable */
}

.btn:disabled:hover {
  background: #ffffff38;
  cursor: not-allowed;
  /* Change the cursor to indicate it's not clickable */

}

.wrapper .register-link {
  font-size: 14.5px;
  text-align: center;
  margin: 20px 0 15px;
}

.register-link p a {
  color: var(--blanco);
  text-decoration: none;
  font-weight: 600;
}

.register-link p a:hover {
  color: var(--shrek);
  text-decoration: none;
  font-weight: 600;
}

.register-link p a:hover {
  text-decoration: underline;
}

.outside-rectangle {
  position: absolute;
  text-align: center;
  bottom: 20px;
  color: var(--blanco99);
  font-size: 14px;
}

